.about-us-banner {
    background: linear-gradient(rgba(0, 0, 0, .55), rgba(0, 0, 0, .45)),
        url(../../../public/assets/informative/aboutUs.png);
    width: 100%;
    height: 310px;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
}

.about-us-banner-heading {
    font-weight: 700;
    font-size: 59.38px;
    color: var(--color-white);
}

.about-us_box1 {
    background: url(../../../public/assets/informative/aboutUsImg2.png);
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 8% 0;
    /* display: flex;
    align-items: center;
    justify-content: center; */
}
.about-us_box1-img {
    width: 100%;
}

.about-us_box1_btn-div {
    margin-top: 3%;
    display: flex;
    align-items: start;
    justify-content: end;
    padding-right: 5%;
}

.about-us_box1_btn {
    background: var(--cold-steel-color);
    padding: 4px 30px 2px 10px;
    text-decoration: none;
    border-radius: 6px;
}

.about-us_box1_icon-span-1 {
    font-size: 10px;
    font-weight: 200;
    color: var(--color-white);
}

.about-us_box1_icon-span-2 {
    color: var(--color-white);
    font-size: 18px;
}

.about-us_box1_icon-span-3 {
    display: block;
    font-weight: 700;
    font-size: 16px;
    padding-top: 2px;
}



.about-us_box2 {
    padding-top: 3.4%;
}

.about-us_box-span1 {
    font-weight: 500;
    display: block;
    font-size: 24px;
    color: var(--carnation-color);
}

.about-us_box-span2 {
    font-weight: 700;
    font-size: 46.08px;
    display: block;
    width: 90%;
    padding: 40px 0;
}

.about-us_box-span3 {
    font-weight: 350;
    font-size: 14px;
    display: block;
    width: 75%;
    color: var(--gray-color);
}